@import "node_modules/bootstrap/scss/functions";

%icon {
    display: inline-block;
    content: "";
}

%icon-primary {
    background-color: theme-color("primary");
}

@mixin icon($path, $width: 1.2rem, $height: 1.2rem, $position-x: null, $position-y: null) {
    @extend %icon;
    mask: $path no-repeat 0 0/#{$width} $height;
    width: $width;
    height: $height;
    @if $position-x {
        margin-left: $position-x;
        margin-right: -$position-x;
    }
    @if $position-y {
        margin-top: -$position-y;
        margin-bottom: $position-y;
    }
}
